<template>
  <div>

    <b-form-input v-model="text1" type="text" placeholder="Something new to do?" @keyup.enter.native="addNewTodo">
    </b-form-input>

    <b-list-group>
      <b-list-group-item class="display-4"><small>{{ $route.params.category }}</small></b-list-group-item>
      <b-list-group-item class="card-text" v-for="(item, i) in items" :key="i" v-if="item.category === $route.params.category">
        {{ item.text }}
        <b-button @click="showModal(); sendIdToModal(item._id);" class="float-right">
          <font-awesome-icon icon="trash-alt" class="float-right"/>
        </b-button>
      </b-list-group-item>
    </b-list-group>

    <div>
      <b-modal ref="myModalRef" hide-header hide-footer size="sm" ok-variant="info">
        <p class="my-4">Are you sure?</p>
        <b-btn class="mt-3 ml-3 float-right" variant="info" @click="hideModal(); removeItem();">Ok</b-btn>
        <b-btn class="mt-3 float-right" variant="seconday" @click="hideModal">Cancel</b-btn>
      </b-modal>
    </div>

  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      text1: "",
      idToModal: ""
    };
  },
  methods: {
    removeItem() {
      axios
        .delete(`/todos/${this.idToModal}`)
        .then(res => {
          this.fetchTodos();
          console.log(res);
        })
        .catch(e => {
          console.log(e);
        });
    },
    addNewTodo() {
      if (this.text1 === "") {
        return;
      }
      axios
        .post("/todos", {
          text: this.text1,
          category: this.$route.params.category
        })
        .then(res => {
          this.text1 = "";
          this.fetchTodos();
          console.log(res);
        })
        .catch(e => {
          console.log("Error:", e);
        });
    },
    showModal() {
      this.$refs.myModalRef.show();
    },
    hideModal() {
      this.$refs.myModalRef.hide();
    },
    sendIdToModal(item) {
      this.idToModal = item;
    }
  },
  props: ["items", "fetchTodos"]
};
</script>

<style scoped>
.completed {
  color: #c6c6c6;
  text-decoration: line-through;
}
</style>