<template>
  <div>
    <b-container>
      <b-row>
        <b-col cols="12">
          <b-link class="link" :to=category.name v-for="(category, i) in categories" :key="i">
            <b-card bg-variant="light" class="text-center mt-3 bcard" border-variant="dark">
              <p class="card-text">{{ category.name }} <b-badge variant="info">{{ itemsCounter(category.name) }}</b-badge></p>
            </b-card>
          </b-link>
          <div class="buttons">
            <b-button class="mt-3 mx-auto catButton" variant="info" size="sm" @click="showModalAdd">New</b-button>
            <b-button class="mt-3 mx-auto catButton" variant="secondary" size="sm" @click="showModalDel">Delete</b-button>
          </div>
        </b-col>
      </b-row>
    </b-container>

<!-- modal delete category -->
    <div>
      <b-modal ref="myModalDel" hide-header hide-footer size="sm" ok-variant="info">
        <p class="my-4">Choose a category and its items to delete:</p>
        <div class="mx-auto buttonsDel">
          <b-button class="delBtn"
            v-for="(category, i) in categories" :key = "i"
            variant="danger"
            @click="deleteCategory(category); hideModalDel();"
            >{{ category.name }}
          </b-button>
        </div>
        <b-btn class="mt-3 float-right" variant="seconday" @click="hideModalDel">Cancel</b-btn>
      </b-modal>
    </div>
<!--  -->

<!-- modal add category -->
    <div>
      <b-modal ref="myModalAdd" hide-header hide-footer size="sm" ok-variant="info">
        <b-form-input v-model="newCatText"
                  type="text"
                  placeholder="Enter name for the new category"></b-form-input>
        <b-btn class="mt-3 ml-3 float-right" variant="info" @click="addNewCategory()">Add</b-btn>
        <b-btn class="mt-3 float-right" variant="seconday" @click="hideModalAdd()">Cancel</b-btn>
      </b-modal>
    </div>
<!--  -->

  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Main",
  props: ["categories", "items", "fetchCategories"],
  data() {
    return {
      newCatText: ""
    };
  },
  methods: {
    itemsCounter(category) {
      var counter = 0;
      for (let item of this.items) {
        if (item.category === category && item.isCompleted === false) {
          counter++;
        }
      }
      return counter;
    },
    showModalDel() {
      this.$refs.myModalDel.show();
    },
    showModalAdd() {
      this.$refs.myModalAdd.show();
    },
    hideModalDel() {
      this.$refs.myModalDel.hide();
    },
    hideModalAdd() {
      this.$refs.myModalAdd.hide();
      this.newCatText = "";
    },
    deleteCategory(category) {
      axios
        .delete(`/categories/${category._id}`)
        .then(res => {
          this.fetchCategories();
        })
        .catch(e => {
          console.log(e);
        });
    },
    addNewCategory() {
      if (this.newCatText === "") {
        return;
      }

      axios
        .post("/categories", {
          name: this.newCatText
        })
        .then(res => {
          this.fetchCategories();
          this.newCatText = "";
          this.hideModalAdd();
        })
        .catch(e => {
          console.log(e);
        });
    }
  }
};
</script>

<style scoped>
.link {
  color: black;
}

.link:hover {
  text-decoration: none;
}

.card-body:hover {
  background-color: #e5e5e5;
}

.card-text {
  font-size: 1.2em;
}

.catButton {
  width: 25%;
}

.buttons {
  display: flex;
}

.buttonsDel > .delBtn {
  margin: 5px;
}

</style>