<template>
  <div id="app">
    <nav>
      <MyNavigation :categories="categories"/>
    </nav>
    <main>
        <router-view :categories="categories" :items="items" :fetchTodos = fetchTodos :fetchCategories = fetchCategories></router-view>
    </main>
  </div>
</template>

<script>
import Navigation from "./components/Navigation";
import axios from "axios";

export default {
  name: "App",
  data() {
    return {
      categories: [],
      items: []
    };
  },
  methods: {
    fetchTodos() {
      axios
        .get("/todos")
        .then(response => {
          this.items = response.data.todos;
        })
        .catch(e => {
          console.log(e);
        });
    },
    fetchCategories() {
      axios
        .get("/categories")
        .then(response => {
          this.categories = response.data.categories;
        })
        .catch(e => {
          console.log(e);
        });
    }
  },
  components: {
    MyNavigation: Navigation
  },
  created() {
    this.fetchTodos();
    this.fetchCategories();
  }
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
}
</style>